import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import styled from 'styled-components';

import type { Evidence } from '@jan6evidence/types';

import { PATHS } from '../../Routes';
import { HighlightContext } from '../../shared/contexts';
import { realTimeRangeFormatter } from '../../shared/util';
import { RAIL_HEIGHT } from '../TimelineHelpers';

const timelineTitle = (evidence: Evidence) => {
  return `[${realTimeRangeFormatter(evidence.realTimeStart, evidence.realTimeEnd)}] ${
    evidence.summary
  }`;
};

const Stripe = styled(Link)`
  display: block;
  position: absolute;

  width: 2px;
  height: ${RAIL_HEIGHT}px;

  transform: translate(-50%, 0);
  margin-top: 0;
  z-index: 2;

  cursor: pointer;

  &:hover,
  &.highlighted {
    background-color: white !important;
  }
`;

type Props = {
  evidence: Evidence;
};

const EvidenceStripe = ({ evidence }: Props) => {
  const location = useLocation();

  return (
    <HighlightContext.Consumer>
      {({ highlight, setHighlight }) => (
        <Stripe
          to={{ pathname: `${PATHS.evidence}/${evidence._id}`, search: location.search }}
          title={timelineTitle(evidence)}
          style={{
            left: `${(evidence.timelineStartPercent || 0) * 100}%`,
            backgroundColor: evidence.color,
          }}
          className={highlight === evidence._id ? 'highlighted' : ''}
          onMouseOver={() => setHighlight(evidence._id)}
          onMouseOut={() => setHighlight('')}
        />
      )}
    </HighlightContext.Consumer>
  );
};

export default React.memo(EvidenceStripe);
