import chroma from 'chroma-js';
import randomSeed from 'random-seed';

import evidenceData from '@jan6evidence/data/evidence.json';
import suspectData from '@jan6evidence/data/suspects.json';
import type { Evidence, Location, Suspect } from '@jan6evidence/types';

// Dates are serailized as strings, so we need to manually deserialize
// Would be nice to move this deserializaiton to ../data/index.ts but we tried
// and ran into compile errors, so we're doing it here for now.
const toDate = (str: string | undefined): Date | undefined => {
  if (!str) {
    return undefined;
  }
  try {
    return new Date(str);
  } catch {
    return undefined;
  }
};

const toLatLng = (location: Location, jitterSeed: string): [number, number] => {
  // jitterSeed with the evidence ID so that it's consistent
  const rand = randomSeed.create(jitterSeed);
  return [
    location.lat + rand.floatBetween(-0.0001, 0.0001),
    location.lng + rand.floatBetween(-0.0001, 0.0001),
  ];
};

export const TIMELINE_DOMAIN_START = Date.UTC(2021, 0, 6, 13, 0, 0); // 8AM Eastern
export const TIMELINE_DOMAIN_END = Date.UTC(2021, 0, 6, 23, 0, 0); // 6PM Eastern

const toTimelinePercent = (time: Date | undefined): number | undefined => {
  if (!time) {
    return undefined;
  }
  return Math.max(
    0,
    Math.min(
      1,
      (time.getTime() - TIMELINE_DOMAIN_START) / (TIMELINE_DOMAIN_END - TIMELINE_DOMAIN_START)
    )
  );
};

// 8am 10am 12pm 2pm 4pm 6pm

const colors = chroma.scale(['#f30', '#ff0', '#099', '#09f']).padding([-0.8, 0]).colors(40);

const makeColor = (timelineStartPercent: number | undefined): string => {
  if (timelineStartPercent === undefined) {
    return '#ccc';
  }
  const colorIndex = Math.floor(timelineStartPercent * colors.length);
  const clampedColorIndex = Math.min(Math.max(colorIndex, 0), colors.length - 1);
  return colors[clampedColorIndex];
};

const evidence = evidenceData.map(
  (item): Evidence => {
    const realTimeStart = toDate(item.realTimeStart);
    const realTimeEnd = toDate(item.realTimeEnd);
    const timelineStartPercent = toTimelinePercent(realTimeStart);
    return {
      ...item,
      realTimeStart,
      realTimeEnd,
      // TODO: move this to the data generation step?
      latLng: item.location ? toLatLng(item.location, item._id) : undefined,
      timelineStartPercent,
      color: makeColor(timelineStartPercent),
    } as Evidence;
  }
);

const suspects = suspectData.map(
  (item): Suspect => {
    const arrestDate = toDate(item.arrestDate);
    return {
      ...item,
      arrestDate,
    } as Suspect;
  }
);

// eslint-disable-next-line import/prefer-default-export
export { evidence, makeColor, suspects, toLatLng, toTimelinePercent };
